.aboutContainer {
  width: 100%;
  display: contents;
  background-color: #c8e9f1;
  padding: 50px;
  font-family: "CaviarDreamsBold";
}

.aboutusContainer {
  justify-content: center;
  align-items: center;
  background-color: #c8e9f1;
  color: rgba(110, 82, 110, 0.863);
  font-family: "CaviarDreamsBold";
  padding-top: 100px;
  padding-bottom: 100px;
}

.aboutus {
  font-family: "CaviarDreamsBold";
  color: rgba(110, 82, 110, 0.863);
  font-family: "CaviarDreamsBold";
  font-size: 40px;
  font-weight: bold;
  padding: 30px;
}

.aboutContent {
  border-radius: 50px;
  font-family: "CaviarDreams";
  color: white;
  padding: 30px;
}

.abouttext {
  display: block;
  padding: 20px;
  color: slategrey;
  font-size: 26px;
  text-align: justify;
  font-family: "CaviarDreams";
}
.investContainer {
  justify-content: center;
  align-items: center;
  display: contents;
  margin: 20px;
  background-color: #c6cde8;
  padding-top: 50px;
  font-family: "CaviarDreams";
}

.invest {
  font-family: "CaviarDreams";
  color: aliceblue;
  font-size: 50px;
  padding-top: 100px;
  font-weight: bold;
  background-color: #c6cde8;
}

.investContent {
  background-color: #c6cde8;
  padding: 75px;
  font-family: "CaviarDreams";
  color: aliceblue;
  text-align: justify;
}

.number {
  font-size: 40px;
  padding-right: 20px;
  align-self: center;
  font-family: "CaviarDreams";
}
.investtext {
  font-size: 24px;
  font-weight: bold;
  font-family: "CaviarDreams";
  display: flex;
  padding: 20px;
}

.whitepapertext {
  margin: 10px;
  font-family: "CaviarDreams";
}
.whitepapertextContainer {
  display: flex;
  padding: 30px;
  justify-content: center;
  border: none;
  flex-direction: column;
  font-size: 24px;
}

.whitepaper {
  text-decoration: #beb7db;
}

.head {
  margin-top: 50px;
  padding: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.headcontainer {
  border-radius: 20px;
  padding: 10px;
  width: fit-content;
  color: #c6cde8;
  font-size: 40px;
  font-family: "CaviarDreams";
}

.teamContainer {
  width: 100%;
}

.profileContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "CaviarDreams";
}

.profile {
  margin: 30px;
  padding-bottom: 10px;
  border-color: #f2d5e7;
  height: max-content;
  color: #c6cde8;
  font-family: "CaviarDreams";
}

.profilePicture > img {
  border-radius: 30px;
}

.profileDescription {
  display: inline-grid;
  font-family: "CaviarDreams";
}

.socialLinks {
  display: flex;
  justify-content: center;
  cursor: pointer;
  font-family: "CaviarDreams";
}

.logo {
  padding: 5px;
}

@media only screen and (max-width: 450px) {
  .teamprofileContainer {
    display: none;
    justify-content: center;
  }
}
