.roadmap {
  display: block;
  padding: 50px;
  background-color: #f2d5e7;
  font-family: "CaviarDreams";
}

.roadmapheader {
  justify-content: left;
  color: rgba(110, 82, 110, 0.863);
  font-family: "CaviarDreams";
  font-weight: bold;
  font-size: 60px;
  padding: 40px;
}

.roadmapContainer {
  color: rgba(110, 82, 110, 0.863);
  width: 60%;
  justify-content: center;
  font-size: small;
  justify-content: flex-end;
  text-size-adjust: inherit;
  display: contents;
  font-family: "CaviarDreams";
}

.quarter {
  font-size: 36px;
  font-weight: bold;
}

.q1Container {
  display: block;
  align-items: center;
  align-self: flex-start;
  justify-content: center;
  font-family: "CaviarDreams";
  padding-top: 50px;
  padding-bottom: 50px;
}

.q2Container {
  display: block;
  align-items: center;
  align-self: flex-end;
  justify-content: center;
  font-family: "CaviarDreams";
  padding-top: 50px;
  padding-bottom: 50px;
}

.q3Container {
  display: block;
  align-items: center;
  justify-content: center;
  font-family: "CaviarDreams";
  padding-top: 50px;
  padding-bottom: 50px;
}

.q4Container {
  display: block;
  align-items: center;
  justify-content: center;
  font-family: "CaviarDreams";
  padding-top: 50px;
  padding-bottom: 50px;
}

.quarterevents {
  border: 5px solid;
  border-radius: 20px;
  border-color: #f2d5e7;
  color: white;
  font-weight: bold;
  background-color: #f2d5e7;
  display: flex;
  justify-content: flex-start;
  padding: 20px;
  font-family: "CaviarDreams";
}

.event {
  font-family: "CaviarDreams";
  color: white;
  font-size: 36px;
  align-self: center;
  padding: 20px;
  width: 50%;
  justify-content: flex-end;
  color: rgba(110, 82, 110, 0.863);
}

.text {
  width: 50%;
  font-size: 20px;
  display: inline;
  text-align: initial;
  font-weight: bolder;
  color: rgba(110, 82, 110, 0.863);
  font-family: "CaviarDreams";
}

@media only screen and (max-width: 450px) {
  .roadmapheader {
    justify-content: left;
    color: rgba(110, 82, 110, 0.863);
    font-family: "CaviarDreams";
    font-weight: bold;
