.accordion {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 50px;
  background-color: #f2d5e7;
  text-align: justify;
}

.accordion-item {
  padding-bottom: 20px;
  margin: 20px;
  padding: 20px;
  color: rgba(110, 82, 110, 0.863);
  cursor: pointer;
}

.accordion-title {
  font-weight: bold;
  font-size: 24px;
  font-family: "CaviarDreams";
}

.accordion-content {
  font-size: 18px;
  font-family: "CaviarDreams";
}
